.screen {
	text-align: center;
	height: calc(100vh - 170px);
	margin-top: 100px;
	margin-bottom: 70px;
	text-align: center;
	display: flex;
	justify-content: top;
	align-items: center;
	flex-direction: column;
	overflow-y: auto;
}

.screen-title {
	color: rgb(41, 72, 102);
	text-decoration: underline;
	margin-left: 10vw;
	margin-right: auto;
	margin-top: 5px;
	margin-bottom: 45px;
}

.history-item {
	height: 100px;
	width: 80vw;
	display: flex;
	align-items: center;
	background-color: white;
	box-shadow: 0px 0px 8px grey;
	border-radius: 20px;
	margin-top: 25px;
	margin-bottom: 5px;
	text-decoration: none;
	color: black;
}

.history-img-container {
	height: 60px;
	width: 60px;
	display: flex;
	align-items: center;
	justify-content: center;
	margin-left: 20px;
}

.history-img-container img {
	height: 60px;
	width: 60px;
	object-fit: contain;
}

.history-name-container {
	height: 100px;
	width: auto;
	display: flex;
	align-items: middle;
	justify-content: center;
	flex-direction: column;
	margin-left: 20px;
}

.history-name {
	font-size: medium;
	display: flex;
	align-items: flex-start;
}

.history-brand {
	color: grey;
	font-size: small;
	display: flex;
	align-items: flex-start;
}

.history-label-container {
	margin-left: auto;
	margin-right: 10px;
}

.history-label {
	color: grey;
	font-size: smaller;
	text-transform: capitalize;
}
